.op-payment-widget-lb-overlay {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 9999; /* op-demo-shop mui has 1100 */
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    overflow-x: hidden;
    transition: 0.5s;
}

.op-payment-widget-lb-overlay-content {
    position: relative;
    width: 100%;
    height: 100%;
}

.op-payment-widget-lb-iframe {
    height: 600px;
    width: 500px;
    background-color: #ffffff;
    border: none;
    border-radius: 4px;
    padding: 10px;
    box-sizing: border-box;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.op-payment-widget-lb-mobile {
    height: 400px;
    width: 250px;
}

.op-payment-widget-lb-small {
    height: 400px;
    width: 390px;
}

.op-payment-widget-lb-landscape {
    height: 400px;
    width: 600px;
}

.op-payment-widget-lb-full-page {
    height: 100%;
    width: 100%;
    border-radius: 0;
}

.op-payment-widget-lb-loaderContainer {
    height: 100vh;
    width: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.op-payment-widget-lb-logo {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    transform: translateY(-200%);
}

.op-payment-widget-lb-loader {
    width: 64px;
    height: 64px;
    color: #6b6b6b;
    margin-bottom: 16px;
    display: inline-block;
    line-height: 1;
    animation: mui-progress-circular-rotate 1.4s linear infinite;
}

.op-payment-widget-hide {
    display: none;
}

.op-payment-widget-lb-loader-circle {
    animation: mui-progress-circular-dash 1.4s ease-in-out infinite;
    stroke-dasharray: 80px, 200px;
    stroke-dashoffset: 0px;
    stroke: currentColor;
}

@-webkit-keyframes mui-progress-circular-rotate {
    100% {
        transform: rotate(360deg);
    }
}

@-webkit-keyframes mui-progress-circular-dash {
    0% {
        stroke-dasharray: 1px, 200px;
        stroke-dashoffset: 0px;
    }
    50% {
        stroke-dasharray: 100px, 200px;
        stroke-dashoffset: -15px;
    }
    100% {
        stroke-dasharray: 100px, 200px;
        stroke-dashoffset: -125px;
    }
}

